<template>
  <main class="community">
    <div class="white-background-wrap">
      <div class="community-intro" id="main" tabindex="0">
        <h2 class="section-header">community<span v-if="$route.params.filterTag">{{' / ' + $route.params.filterTag}}</span><span v-if="$route.name === 'Equipment'"> / equipment</span></h2>
        <p>{{sectionText}}</p>
      </div>
    </div>
    <transition name="fade" mode="out-in">
      <router-view :pageType='"Community"'></router-view>
    </transition>
  </main>
</template>

<script>
export default {
  name: 'community',
  computed: {
    sectionText: function () {
      if (this.$route && this.$route.params) {
        switch (this.$route.params.filterTag) {
          case 'giveaways':
            return 'Check back every month for our epic community giveaway, these prizes are the real deal. Past giveaways have included AAA games, Razer Naga Trinity gaming mouse, and the Nintendo Switch!'
          case 'faq':
            return 'What games has Lowco played? Why is chat talking about candy? What even is a FishMoley? And other useful information about the channel!'
          case 'events':
            return 'Our bond is what sets The Looneybin apart from so many other communites on the web!'
          case 'equipment':
            return 'It takes serious hardware to bring Lowco\'s pro strats to your monitor!'
          default:
            return 'A repository for all things Looney! Find information about Lowco\'s streaming equipment, our current monthly giveaway, upcoming events and more!'
        }
      } else {
        return 'A repository for all things Looney! Find information about Lowco\'s streaming equipment, our current monthly giveaway, upcoming events and more!'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
.community-intro{
  padding: $home-section-padding;
  max-width: 600px;
  height: 162px;
  margin: 4em auto 0 auto;
}
</style>



